function HomeBody({ currentModel, handleModel, go }) {
  return (
    <div className="home__body">
      <div className="home__title title">
          <span className="text_accented"></span>
      </div>
      <div className="home__row">
        <div
          className={`models-item ${
            currentModel == "Protogen" && "models-item_active"
          }`}
          onClick={handleModel}
        >
          <input
            type="radio"
            id="Protogen"
            name="models"
            className="models-item__radio"
            defaultChecked
          ></input>
          <label htmlFor="Protogen">
            <div className="models-item__title mini-title"></div>
            <div className="models-item__text text">Protogen</div>
          </label>
        </div>
        <div
          className={`models-item ${
            currentModel == "Counterfeit" && "models-item_active"
          }`}
          onClick={handleModel}
        >
          <input
            type="radio"
            id="Counterfeit"
            name="models"
            className="models-item__radio"
          ></input>
          <label htmlFor="Counterfeit">
            <div className="models-item__title mini-title"></div>
            <div className="models-item__text text">Counterfeit</div>
          </label>
        </div>
        <div
          className={`models-item ${
            currentModel == "Rev anim" && "models-item_active"
          }`}
          onClick={handleModel}
        >
          <input
            type="radio"
            id="Rev anim"
            name="models"
            className="models-item__radio"
          ></input>
          <label htmlFor="Rev anim">
            <div className="models-item__title mini-title"></div>
            <div className="models-item__text text">Rev Anim</div>
          </label>
        </div>
      </div>
      <div
        className="home__btn btn"
        onClick={() => go()}
      >{` ${currentModel}`}</div>
    </div>
  );
}

export default HomeBody;
